.drag {
    position: absolute;
    height: 120px;
    width: 120px;
    background-color: #ec625c;
    cursor: grab;
    touch-action: none;
    -webkit-user-select: none;
    user-select: none;
    font-size: 10px;
  }
  
  .drag > div {
    margin: 10%;
    width: 80%;
    height: 80%;
    background-color: #000;
    color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: monospace;
  }
  
  .hover {
    height: 200px;
    width: 200px;
    background-color: royalblue;
  }
  
  .hover:hover {
    background-color: darkblue;
  }